.authNav {
  display: flex;
  gap: 100px;
  width: (260px);
}

@media (max-width: 454px) {
  .authNav {
    gap: 10px;
  }
}

.link {
  text-decoration: none;
  font-size: 25px;
  color: #989797;
}

.link:hover,
.link:focus {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
  color: #1565c0;
  outline: none;
}
